import { Box, Collapse } from "@mui/material";

const ChatInfo = () => {
  return (
    <Box width="100%" maxWidth={256}>
      <Collapse
        in={true}
        sx={{ bgcolor: "#ffffff", height: "100%", "& *": { height: "100%" } }}
      >
        <Box
          display="flex"
          height="100%"
          justifyContent="center"
          alignItems="center"
        >
          Info Drawer
        </Box>
      </Collapse>
    </Box>
  );
};

export default ChatInfo;
