import { Menu, MenuItem } from "@mui/material";
import { ChatOptionsSX } from "Containers/Engage/Chat/LayoutComponents";

type ChatOptionsProps = {
  open: boolean;
  anchorEl: null | HTMLElement;
  doClose: () => void;
};
const ChatOptions = ({ open, anchorEl, doClose }: ChatOptionsProps) => {
  return (
    <Menu
      anchorEl={anchorEl}
      open={open}
      onClose={doClose}
      onClick={() => doClose()}
      keepMounted
      MenuListProps={{
        "aria-labelledby": "Options",
      }}
      anchorOrigin={{
        vertical: "bottom",
        horizontal: "right",
      }}
      transformOrigin={{
        vertical: "top",
        horizontal: "right",
      }}
      sx={ChatOptionsSX}
    >
      <MenuItem>{"+ Add a Tab"}</MenuItem>
      <MenuItem>{"Manage Tabs"}</MenuItem>
    </Menu>
  );
};

export default ChatOptions;
