import { Fragment, MouseEvent, SyntheticEvent, useState } from "react";
import { useSearchParams } from "react-router-dom";
import {
  Box,
  Checkbox,
  IconButton,
  List,
  ListItem,
  ListItemButton,
  Typography,
} from "@mui/material";
import {
  ArrowDropDown,
  FilterAltOutlined,
  MoreVert,
  Search,
} from "@mui/icons-material";

import { useChatData } from "Contexts/Engage/Chat";
import {
  ChatListContent,
  ChatListData,
  ChatListHeader,
  ChatListTab,
  ChatListTabs,
  ChatListTabsSx,
  ChatListTabsWrapper,
  ChatListWrapper,
  ChatListWrapperSx,
  ListHeaderButtonSX,
  ListTabButtonSX,
} from "Containers/Engage/Chat/LayoutComponents";

import ChatOptions from "./ChatOptions";
import ChatBulkAction from "./ChatBulkAction";

const ChatList = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const { chatData } = useChatData();
  const [moreOptEl, setMoreOptEl] = useState<HTMLButtonElement | null>(null);
  const [bulkActionEl, setBulkActionEl] = useState<HTMLButtonElement | null>(
    null
  );
  const [currentTab, setCurrentTab] = useState<string>(
    searchParams.get("tab") ?? "all"
  );
  const [checkAll, setCheckAll] = useState(false);
  const [checked, setChecked] = useState<number[]>([]);
  const openMoreOpt = Boolean(moreOptEl);
  const openBulkAction = Boolean(bulkActionEl);
  const changeTab = (_event: SyntheticEvent, newValue: string) => {
    setCurrentTab(newValue);
    setSearchParams({ tab: newValue });
  };
  const doCheckAll = (event: React.ChangeEvent<HTMLInputElement>) => {
    setCheckAll(event.target.checked);
    if (event.target.checked) {
      // setChecked();
    } else {
      setChecked([]);
    }
  };
  const doCheck = (value: number) => () => {
    console.log(checked, value);
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];
    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }
    setChecked(newChecked);
  };

  return (
    <Fragment>
      <ChatListWrapper sx={ChatListWrapperSx}>
        <ChatListTabsWrapper>
          <ChatListTabs
            value={currentTab}
            onChange={changeTab}
            variant="scrollable"
            orientation="horizontal"
            scrollButtons="auto"
            allowScrollButtonsMobile
            sx={ChatListTabsSx}
          >
            <ChatListTab label="All" value="all" />
            <ChatListTab label="Follow Up" value="follow-up" />
            <ChatListTab label="Support" value="support" />
            <ChatListTab label="Calling List" value="calling-list" />
            <ChatListTab label="Interested" value="interested" />
          </ChatListTabs>
          <IconButton
            onClick={(event: MouseEvent<HTMLButtonElement>) =>
              setMoreOptEl(event.currentTarget)
            }
            sx={ListTabButtonSX}
          >
            <ArrowDropDown />
          </IconButton>
        </ChatListTabsWrapper>
        <ChatListContent>
          <ChatListHeader>
            <Checkbox
              checked={checkAll}
              onChange={doCheckAll}
              inputProps={{ "aria-label": "Check All" }}
              sx={{
                mr: "auto",
              }}
            />
            <IconButton sx={ListHeaderButtonSX}>
              <Search />
            </IconButton>
            <IconButton sx={ListHeaderButtonSX}>
              <FilterAltOutlined />
            </IconButton>
            <IconButton
              onClick={(event: MouseEvent<HTMLButtonElement>) =>
                setBulkActionEl(event.currentTarget)
              }
              sx={ListHeaderButtonSX}
            >
              <MoreVert />
            </IconButton>
          </ChatListHeader>
          <ChatListData>
            <List sx={{ p: 0 }}>
              {chatData.map((data, index: number) => (
                <ListItem
                  key={index}
                  secondaryAction={
                    <Checkbox
                      edge="end"
                      onChange={doCheck(index)}
                      checked={checkAll || checked.indexOf(index) !== -1}
                    />
                  }
                  disablePadding
                  sx={{
                    "& .MuiListItemSecondaryAction-root": {
                      right: "unset",
                      left: 0,
                    },
                  }}
                >
                  <ListItemButton sx={{ pl: 6, pr: 2 }}>
                    <Box>
                      <Box>
                        <Typography>{data.lead_data.name}</Typography>
                      </Box>
                      <Box></Box>
                    </Box>
                  </ListItemButton>
                </ListItem>
              ))}
            </List>
          </ChatListData>
        </ChatListContent>
      </ChatListWrapper>
      <ChatOptions
        open={openMoreOpt}
        anchorEl={moreOptEl}
        doClose={() => setMoreOptEl(null)}
      />
      <ChatBulkAction
        open={openBulkAction}
        anchorEl={bulkActionEl}
        doClose={() => setBulkActionEl(null)}
      />
    </Fragment>
  );
};

export default ChatList;
