/* eslint-disable react-hooks/exhaustive-deps */
import { createContext, ReactNode, useContext, useMemo, useState } from "react";
import {
  ChatApiType,
  ChatDataSet,
  ChatDataType,
} from "Shared/Interfaces/Engage/Chat";

const ChatData = createContext<ChatDataType | undefined>(undefined);
const ChatAPI = createContext<ChatApiType | undefined>(undefined);

export const ChatProvider = ({ children }: { children: ReactNode }) => {
  const [chatData, setChatData] = useState<ChatDataSet[]>([
    {
      id: 107284,
      auto_reminder: true,
      assigned_to: null,
      platform_id: 1,
      business_id: 1,
      status: "new",
      contact_number: "918858997164",
      disengage: false,
      assign_attempted: false,
      origin: "WA",
      platform: "WHATSAPP",
      follow_up_time: null,
      is_chat_initiated: true,
      ar_status: true,
      m_id: "62c2e109e9837132dfc0fc8c",
      created_at: "2022-07-04T12:46:01.716+00:00",
      updated_at: "2022-09-18T02:50:45.675+00:00",
      lead_data: {
        name: "Parag",
        assigned: false,
        created_at: "2022-07-04T12:46:01.714Z",
        updated_at: "2022-09-18T02:50:44.476Z",
      },
      reminder: {
        date: null,
        active: false,
      },
      assigned_user: {
        name: "RAJINDRA HONDA",
        id: 1733,
      },
      last_message: {
        id: "1146984",
        lead_id: 107284,
        business_id: 1,
        type: "image",
        phone_number_id: "109793818525994",
        contact_number: "918858997164",
        sent_by: "self",
        country_code: "+91",
        message: {
          link: "https://storage.googleapis.com/es-chatflow-files/71fa1bab-618b-4657-8c02-175e60356a8c-HondaLogo.jpeg",
          caption:
            "Welcome to Honda Motorcycle and Scooter India Pvt. Ltd.\n\nWe are happy to help you today!\n\nPlease tell us *your name*.",
        },
        message_id: "d8856ba4-0d38-48dd-9af0-93f90234d9e9",
        platform: "WHATSAPP",
        provider: "360dialog",
        status: "delivered",
        created_at: "2022-09-18T02:50:45.665+00:00",
        updated_at: "2022-09-18T02:50:47.121+00:00",
      },
    },
  ]);
  const api = useMemo<ChatApiType>(() => ({ setChatData }), []);

  return (
    <ChatData.Provider value={{ chatData }}>
      <ChatAPI.Provider value={api}>{children}</ChatAPI.Provider>
    </ChatData.Provider>
  );
};

export const useChatData = () => {
  const context = useContext(ChatData);
  if (context === undefined) {
    throw Error(
      "Item must be used inside of ChatProvider, otherwise it will not function correctly."
    );
  }
  return context;
};

export const useChatApi = () => {
  const context = useContext(ChatAPI);
  if (context === undefined) {
    throw Error(
      "Item must be used inside of ChatProvider, otherwise it will not function correctly."
    );
  }
  return context;
};
