import { ReactNode } from "react";
import { Box, Tab, Tabs } from "@mui/material";
import { styled } from "@mui/system";

export const FullSizeSpaceBetweenFlexBox = ({
  children,
}: {
  children: ReactNode;
}) => (
  <Box display="flex" width="100%" height="100%" justifyContent="space-between">
    {children}
  </Box>
);

export const ChatMainContent = styled("div")({
  flex: "1 0 0",
  height: "100%",
  display: "flex",
  flexDirection: "column",
  overflowX: "hidden",
  overflowY: "auto",
});

export const ChatListWrapper = styled("div")({
  backgroundColor: "#ffffff",
  borderRight: 1,
  borderColor: "#0000000D",
  display: "flex",
  flexDirection: "column",
  width: "100%",
});

export const ChatListWrapperSx = {
  maxWidth: { md: 380, xl: 480 },
};

export const ChatListTabsWrapper = styled("div")({
  borderBottom: 1,
  borderBottomStyle: "solid",
  borderBottomColor: "#0000000D",
  display: "flex",
  alignItems: "center",
});

export const ChatListTabs = styled(Tabs)({
  flexGrow: 1,
  minHeight: 46,
  paddingLeft: 14,
});

export const ChatListTabsSx = {
  minHeight: 46,

  "& .MuiTabScrollButton-root": {
    alignSelf: "center",
    borderRadius: 4,
    height: 32,
    width: 32,
  },

  "& .MuiTabs-indicator": {
    height: 4,
  },
};

export const ChatListTab = styled(Tab)({
  fontSize: 16,
  fontWeight: 500,
  lineHeight: "19px",
  minHeight: 46,
  minWidth: "unset",
  paddingLeft: 10,
  paddingRight: 10,
  textTransform: "inherit",
});

export const ChatOptionsSX = {
  "& .MuiPaper-root": {
    minWidth: 140,

    "& .MuiList-root": {
      p: 0,

      "& .MuiMenuItem-root": {
        color: "#12B0EE",
        justifyContent: "center",
        minHeight: 40,
        fontSize: 16,
        fontWeight: 500,
        lineHeight: "19px",

        "&:not(:last-of-type)": {
          borderBottom: 1,
          borderBottomColor: "#0000000D",
        },
      },
    },
  },
};

export const ListTabButtonSX = { width: 42, height: 42 };

export const ListHeaderButtonSX = { width: 42, height: 42 };

export const ChatListContent = styled("div")({
  display: "flex",
  flexDirection: "column",
  flexGrow: 1,
});

export const ChatListHeader = styled("div")({
  display: "flex",
  flex: "0 1 0",
  paddingLeft: 14,
});

export const ChatListData = styled("div")({
  flex: "1 0 0",
  overflow: "auto",
  paddingLeft: 14,
  paddingRight: 14,
});
