import { ReactNode } from "react";

import { ChatProvider } from "Contexts/Engage/Chat";

import { FullSizeSpaceBetweenFlexBox } from "./Chat/LayoutComponents";

type ChatContainerProps = {
  children: ReactNode;
};

const ChatContainer = ({ children }: ChatContainerProps) => {
  return (
    <ChatProvider>
      <FullSizeSpaceBetweenFlexBox>{children}</FullSizeSpaceBetweenFlexBox>
    </ChatProvider>
  );
};

export default ChatContainer;
