import { Helmet } from "react-helmet-async";

import ChatContainer from "Containers/Engage/Chat";

import ChatList from "./Chat/List";
import ChatMain from "./Chat/Main";

const Chat = () => {
  return (
    <ChatContainer>
      <Helmet>
        <title>Engage Chat</title>
      </Helmet>
      <ChatList />
      <ChatMain />
    </ChatContainer>
  );
};

export default Chat;
