import { Menu, MenuItem } from "@mui/material";

type ChatBulkActionProps = {
  open: boolean;
  anchorEl: null | HTMLElement;
  doClose: () => void;
};
const ChatBulkAction = ({ open, anchorEl, doClose }: ChatBulkActionProps) => {
  return (
    <Menu
      anchorEl={anchorEl}
      open={open}
      onClose={doClose}
      keepMounted
      MenuListProps={{
        "aria-labelledby": "Bulk Actions",
      }}
      anchorOrigin={{
        vertical: "bottom",
        horizontal: "right",
      }}
      transformOrigin={{
        vertical: "top",
        horizontal: "right",
      }}
      sx={{
        "& .MuiPaper-root": {
          minWidth: 140,

          "& .MuiList-root": {
            p: 0,

            "& .MuiMenuItem-root": {
              color: "#12B0EE",
              justifyContent: "center",
              minHeight: 40,
              fontSize: 16,
              fontWeight: 500,
              lineHeight: "19px",

              "&:not(:last-of-type)": {
                borderBottom: 1,
                borderBottomColor: "#0000000D",
              },
            },
          },
        },
      }}
    >
      <MenuItem
        onClick={() => {
          doClose();
        }}
      >
        {"Action 1"}
      </MenuItem>
      <MenuItem
        onClick={() => {
          doClose();
        }}
      >
        {"Action 2"}
      </MenuItem>
    </Menu>
  );
};

export default ChatBulkAction;
